.snip0016 {
font-family: 'Raleway', Arial, sans-serif;
color: #fff;
position: relative;
float: left;
overflow: hidden;
margin: 20px;
max-height: 450px;
width: 100%;
text-align: left;
border-radius: 0 30px 0 30px;

}
.snip0016,
.sinp-img{
max-width: 100%;
opacity: 1;
width: 100%;
transition: opacity 0.35s;

}
.snip0017 {
position: absolute;
bottom: 0;
left: 0;
padding: 30px 3em;
width: 100%;
height: 100%;

}
.snip0017::before {
position: absolute;
top: 30px;
right: 30px;
bottom: 30px;
left: 100%;
border-left: 4px solid rgba(255, 255, 255, 0.8);
content: '';
opacity: 0;
background-color: rgba(255, 255, 255, 0.5);
transition: all 0.5s;
transition-delay: 0.6s;
}
.snip0016 h2,
.snip0016 p {
margin: 0 0 5px;
opacity: 0;
transition: opacity 0.35s,-webkit-transform 0.35s,transform 0.35s;
}
div.snip0016 h2 {
word-spacing: -0.15em;
font-weight: 300;
text-transform: uppercase;
-webkit-transform: translate3d(30%, 0%, 0);
transform: translate3d(30%, 0%, 0);
transition-delay: 0.3s;
}
.snip0016 h2 span {
font-weight: 800;
}
.snip0016 p {
font-weight: 200;
-webkit-transform: translate3d(0%, 30%, 0);
transform: translate3d(0%, 30%, 0);
transition-delay: 0s;
}
div.snip0016:hover img {
opacity: 0.3;
}
.snip0016:hover .snip0017 h2 {
opacity: 1;
-webkit-transform: translate3d(0%, 0%, 0);
transform: translate3d(0%, 0%, 0);
transition-delay: 0.4s;
}
.snip0016:hover .snip0017 p {
opacity: 0.9;
-webkit-transform: translate3d(0%, 0%, 0);
transform: translate3d(0%, 0%, 0);
-webkit-transition-deldivy: 0.6s;
transition-delay: 0.6s;
}

.snip0016:hover .snip0017::before {
background: rgba(255, 255, 255, 0);
left: 30px;
opacity: 1;
transition-delay: 0s;
}






