.Projects-container{
  background: #1f2235;
    text-align: center;
    
}
.png{
    width: 400px;
    margin: 20px 10px 20px 10px;
    transition: 0.3s;
    z-index: 10;
}

.png:hover {
    transform: scale(1.2); 
    transition: 0.3s; 
  }
  
@media (min-width: 200px) and (max-width: 729px) {
    .png{
        width: 300px;
    }
  }
  
  
  @media (min-width: 729px) and (max-width: 960px){
    .png{
        width: 600px;
        margin: 20px 10px 20px 10px;
    }
  }